body {
  height: 800px;
  background-image: linear-gradient(skyblue, pink);
}

.inform {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid black;
  background-color: palevioletred;
}

.data {
  display: flex;
  justify-content: center;
  background-color: plum;
  border: 1px solid black;
  margin: 20px;
}

.inside {
  padding: 5px;
  font-size: 30px;
}

form {
  justify-content: center;
  align-items: center;
}

label {
  font-size: 30px;
  margin: 20px;
}

input {
  font-size: 30px;
  border: 1px solid lightblue;
  border-radius: 50px;
  height: 50px;
  margin: 10px;
}

.btn {
  height: 50px;
  width: 200px;
  font-size: 30px;
  background-color: skyblue;
  border: 2px solid black;
  border-radius: 50px;
  margin: 10px;
}

.btn:hover {
  background-color: lightseagreen;
}
